@mixin web-interface-form {
  max-width: $text-block-width;
  padding: $lem;

  display: none;
  flex-direction: column;

  .title {
    display: flex;
    justify-content: center;
    flex-basis: 100%;

    @include text-style(headline-3);
    text-align: center;
    margin: $lem 0;
  }

  .form {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    flex-basis: 100%;

    @include form;

    .fieldset {
      display: flex;
      flex-direction: column;
    }

    .agreement {
      text-align: center;
      margin: $lem 0;
    }

    .button {
      @include button;
      @include colors(white-on-red);
      border: none;
    }
  }
}
