// features page
//
// The features page
//
// Styleguide features
html {
  &.features {
    body {
      @include features--gradients;

      .menu {
        .menu-item {
          &:nth-of-type(2) {
            .link {
              @include link(white-on-transparent, white-on-transparent, white-on-transparent);
            }
          }
        }
      }

      .features {
        @include features;
      }
    }
  }
}
