@mixin publishers__mockups--mobile {
  @include mobile {
    height: calc(56.24vw + #{$lem});

    .mockup {
      &:first-of-type {
        transform: scale(.4) translateX(-$lem * 5) translateY($lem * 4);
      }

      &:last-of-type {
        .mockup--browser {
          transform: rotateY(0deg);

          .mockup__buttons {
            padding: $lem / 6 0;

            .circle {
              @include circle($lem / 8);
            }
          }

          .mockup__iframe {
            @include mockup__iframe(70vw, 16, 9);
          }
        }
      }
    }
  }
}
