@mixin team {
  @include section-fitscreen($portrait-padding: true);

  display: flex;
  flex-direction: column;
  justify-content: space-around;
  align-items: center;

  &__hero {
    @include team__hero;
  }

  .articles {
    @include articles(4, $lem * 5);
    @include section-margin;

    margin-top: $lem;
    align-items: flex-start;

    .article {
      > * {
        order: 4;
      }

      &__thumb {
        order: 1;
      }

      &__subtitle {
        padding: $lem / 4 0;
        order: 2;
      }

      &__category {
        order: 3;
      }

      &__excerpt {
        padding: $lem 0;
        @include colors(gray-team-on-black);
      }
    }
  }
}
