@mixin hero__sliders {
  position: relative;

  $w: $lem * 15;
  $b: $lem * 1.5;

  .range {
    @include transition(opacity);
    display: none;

    &.device-range-horizontal {
      @include range($w, $b);
    }

    &.device-range-vertical {
      @include range($w, $b, vertical);

      position: absolute;
      right: 20vw;
      top: 45vh;
    }
  }

  @include hero__sliders--mobile;
}
