@mixin features__articles {
  @include colors(black-on-white);
  @include section-padding;

  display: flex;
  flex-direction: column;

  @include features__articles--mobile;
  @include features__articles--mobile-and-up;
  @include features__articles--tablet;

  .article {
    @include article;
    @include article--horizontal;

    &__thumb {
      .figure {
        @include colors(white-on-black);
      }
    }

    &__excerpt {
      padding: $lem 0;
    }

    &__list {
      @include ul--with-bullet($blue);
    }

    &__excerpt,
    &__list {
      @include colors(gray-footer-on-transparent);
    }
  }

  .horizontal-line {
    display: flex;
    align-self: center;
    padding-top: 10vh;
    padding-bottom: 10vh;
  }
}
