@mixin rich-media {
  @include colors(white-on-green);

  @include section-fitscreen($portrait-padding: true);

  display: flex;
  flex-direction: column;

  &__headline {
    @include rich-media__headline;
  }

  .rich-media-hero {
    @include rich-media-hero;
  }

  .articles {
    @include articles($margin-right: $lem * 5);
    @include section-margin;

    .article {
      &__subtitle {
        padding: $lem / 4 0;
      }

      &__excerpt {
        max-width: 14em;
      }
    }
  }

  @include rich-media--tablet-and-below;
}
