@mixin contact {
  display: flex;
  flex-direction: column;

  &__title {
    display: flex;

    @include text-style(headline-2);
    padding: $lem / 4 0;
  }

  .ul {
    @include ul;
    @include ul--horizontal;

    justify-content: flex-start;
    @include text-style(footer);

    padding-top: $lem / 2;

    .li {
      padding-bottom: $lem / 2;
      
      .link {
        @include link(blue-on-white, blue-on-white, blue-on-white);
      }

      &.phone {
        @include colors(gray-footer-on-white);

        .bull {
          padding: 0 $lem / 2;
        }
      }
    }
  }

  .articles {
    @include articles(2);

    .article {
      &__thumb {
        order: 2;
      }

      &__title {
        @include text-style(footer);
        order: 1;
        padding: $lem / 2 0;
      }
    }
  }

  @include contact--portrait;
}
