@mixin slide2 {
  position: absolute;
  left: 0;
  top: 0;

  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;;

  opacity: .5;
  cursor: pointer;
  @include transition(transform);

  .picture {

  }

  &--active,
  &:hover {
    @include slide2--active;
  }
}
