@mixin advertisers__mockups {
  display: flex;
  flex-direction: column;
  position: relative;

  margin: $lem 0;

  .mockup {
    @include mockup($button-colors: true);

    position: relative;
    height: $lem * 2;
    width: 90%;

    &:nth-of-type(1) {
      z-index: 1;
    }

    &:nth-of-type(2) {
      z-index: 2;
      margin-left: 5%;
    }

    &:nth-of-type(3) {
      z-index: 3;
      margin-left: 10%;
    }
  }

  @include advertisers__mockups--landscape;
  @include advertisers__mockups--tablet-and-below;
  @include advertisers__mockups--tablet-portrait;
  @include advertisers__mockups--mobile;
}
