@mixin footer-social {
  &__title {
    display: flex;

    @include text-style(headline-2);
    padding: $lem / 4 0;
  }

  .ul {
    @include ul;
    @include text-style(footer);
    padding-top: $lem / 2;

    .li {
      padding-bottom: $lem / 2;

      .link {
        @include link(blue-on-white, blue-on-white, blue-on-white);
      }
    }
  }
}
