@mixin web-interface__grid {
  position: relative;

  &__slides,
  &__description {
    position: relative;
  }

  .slides {
    position: relative;
    width: 100%;

    .article {
      position: absolute;
      width: 50vw;
      z-index: 1;

      &__button {
        top: -$lem * 4;
      }

      &:nth-of-type(1) {
        left: -15vw;

        .article__button {
          left: 1.25vw;
        }
      }

      &:nth-of-type(2) {
        top: -4.5em;
        left: 20vw;
        width: 60vw;

        .img {
          width: 60vw;
        }

        z-index: 2;
      }

      &:nth-of-type(3) {
        right: -15vw;

        .article__button {
          left: -1.25vw;
        }
      }
    }
  }

  @include web-interface__grid--portrait;
  @include web-interface__grid--mobile;
  @include web-interface__grid--landscape;
  @include web-interface__grid--desktop;
}
