@mixin features__hero {
  @include section-fitscreen($portrait-padding: true);
  min-height: $lem * 16;
  // The height of the header must be added here, since the header has position: absolute
  margin-top: $lem * 3;

  display: flex;
  flex-direction: column;
  align-items: center;

  .title {
    display: flex;
    padding: $lem;

    @include text-style(hero);
    @include text-centered;
  }

  .horizontal-line {
    @include horizontal-line($lem * 10, $black-features-hero-line);
  }

  .features__filters {
    @include features__filters;
  }
}
