@mixin article__subtitle-with-image {
  display: flex;
  align-items: center;

  @include article__subtitle-with-image--landscape;

  .article__thumb {
    @include article__thumb;
    margin-right: $lem / 2;

    .img {
      width: 52px;
    }
  }

  .article__subtitle {
    @include article__subtitle;
  }
}
