@mixin everything__articles {
  @include articles($margin-right: $lem * 2);
  @include section-margin;

  .article {
    @include colors(black-on-white);
    padding: $lem * 3;
    justify-content: center;

    &__icon {
      .icon {
        @include icon($lem * 2, $lem * 2, $green);
      }
    }

    &__subtitle {
      padding: $lem 0;
    }

    &__excerpt {
      max-width: 14em;
    }

    &__button {
      padding: $lem * 3 0 $lem;

      .button {
        @include colors(white-on-red);
      }
    }
  }

  @include everything__articles--tablet;
  @include everything__articles--desktop;
}
