@mixin publishers__mockups--tablet-and-below {
  @include tablet-and-below {
    height: calc(56.24vw - #{$lem * 2});

    align-items: center;
    justify-content: center;

    .mockup {
      &:first-of-type {
        transform: scale(.8) translateX(0) translateY(12em);
      }

      &:last-of-type {
        .mockup__iframe {
          @include mockup__iframe(65vw, 16, 9);
        }
      }
    }
  }
}
