@mixin hero-mockup--reposition {
  &--tablet {
    top: 9em;
  }

  &--phone {
    transform: translateY(9.5em);
  }


  @include mobile {
    &--tablet {
      top: 0em;
    }

    &--phone {
      transform: translateY(-1em);
    }
  }


  @include desktop {
    &--phone {
      transform: translateY(12em);
    }
  }
}
