@mixin features__articles--mobile-and-up {
  @include mobile-and-up {
    .article {
      &:nth-of-type(2n + 2) {
        .article__thumb {
          order: 2;
          margin-right: 0;
        }

        .article-container {
          padding: 0 $lem * 3;
        }
      }
    }

    .horizontal-line {
      @include horizontal-line(70vh, $gray-format);
    }
  }
}
