@mixin publishers__mockups--landscape {
  @include landscape {
    width: 50vw;
    height: calc(50vw / 1.778 + #{$lem * 2 * 2});

    .mockup {
      &:first-of-type {
        transform: scale(.7) translateX(-$lem * 4) translateY($lem * 10);
      }

      &:last-of-type {
        .mockup__iframe {
          @include mockup__iframe(50vw, 16, 9);
        }
      }
    }
  }
}
