@mixin mockup--android {
  .figure {
    @include figure;
  }

  .mockup__iframe {
    @include mockup__iframe(71%, 2, 3);

    width: 73%;
    top: 10%;
    left: 13%;
    position: absolute;
    perspective: 2000px;

    .intrinsic-ratio-container {
      padding-bottom: 171%;
      transform: rotateY(12.5deg);
    }
  }
}
